function ajax(params) {
  params = params || {}
  params.data = params.data || {}

  // 
  function random() {
    return Math.floor(Math.random() * 10000 + 500)
  }

  // 
  function formatParams(data) {
    var arr = []

    for (let name in data) {
      // encodeURIComponent()  URI 
      arr.push(encodeURIComponent(name) + '=' + encodeURIComponent(data[name]))
    }
    // 
    arr.push('v=' + random())
    return arr.join('&')
  }

  // ajax
  function json(params) {
    // GET
    params.type = (params.type || 'GET').toUpperCase()
    // 
    params.data = formatParams(params.data)
    let xhr = null

    // XMLHttpRequest
    if (window.XMLHttpRequest) {
      xhr = new XMLHttpRequest()
    } else {
      // IE6
      // eslint-disable-next-line no-undef
      xhr = new ActiveXObjcet('Microsoft.XMLHTTP')
    }

    //  readyState  readystatechange 
    xhr.onreadystatechange = function () {
      // readyState/4
      if (xhr.readyState === 4) {
        let status = xhr.status
        // statusHTTP2

        if (status >= 200 && status < 300) {
          let response = ''
          // 
          let type = xhr.getResponseHeader('Content-type')

          if (type.indexOf('xml') !== -1 && xhr.responseXML) {
            response = xhr.responseXML // Document
          } else if (type === 'application/json') {
            response = JSON.parse(xhr.responseText) // JSON
          } else {
            response = xhr.responseText // 
          }
          // 
          params.success && params.success(response)
        } else {
          params.error && params.error(status)
        }
      }
    }

    // 
    if (params.type === 'GET') {
      // (get)()
      xhr.open(params.type, params.url + '?' + params.data, true)
      xhr.send(null)
    } else {
      xhr.open(params.type, params.url, true)
      // 
      xhr.setRequestHeader(
        'Content-Type',
        'application/x-www-form-urlencoded; charset=UTF-8'
      )
      // 
      xhr.send(params.data)
    }
  }

  // jsonp
  function jsonp(params) {
    // script
    var callbackName = params.jsonp
    var head = document.getElementsByTagName('head')[0]
    // 

    params.data['callback'] = callbackName
    let data = formatParams(params.data)
    let script = document.createElement('script')
    // let responseData

    head.appendChild(script)

    // jsonp
    window[callbackName] = function (json) {
      head.removeChild(script)
      clearTimeout(script.timer)
      window[callbackName] = null
      // responseData = json
      params.success && params.success(json)
    }

    // 
    script.src = params.url + '?' + data
    // scriptloaderror

    document.addEventListener(
      'error',
      function (event) {
        if (event) {
          let target = event.target

          if (target === script) {
            script.timer && clearTimeout(script.timer)
            params.error &&
              params.error(
                Object.assign(
                  {
                    message: 'js'
                  },
                  params
                )
              )
          }
        }
      },
      true
    )

    // 
    if (params.time) {
      script.timer = setTimeout(function () {
        window[callbackName] = null
        head.removeChild(script)
        params.error &&
          params.error({
            message: ''
          })
      }, params.time)
    }
  }
  // ajaxjsonp
  params.jsonp ? jsonp(params) : json(params)
}

export default ajax
