import ajax from './http'
import $ebg from '../components/message'
const strAssert = (str) => {
  return !(str == null || str.length < 1)
}
const alertErrorIfStrEmpty = (str, errorMsg) => {
  if (!strAssert(str)) {
    $ebg.confirm({
      title: '',
      text: errorMsg,
      confirm: ''
    })
    return false
  }
  return true
}
// 
const appEnterable = (storeAppid, hosturl, apiUrl, callback) => {
  // 0 
  let enable = false
  ajax({
    type: 'GET',
    url: hosturl + apiUrl.appEnterable,
    jsonp: 'jsonp',
    async: false, // 
    data: { appId: storeAppid },
    success: function (result) {
      if (result['success'] === true) {
        let data = result['data']
        if (data['enable'] === false) {
          // 
          let msg = ''
          if (data['isAdmin'] === true) {
            msg =
              '--'
          } else {
            msg =
              ''
          }
          $ebg.confirm({
            title: '',
            text: msg,
            confirm: ''
          })
        } else {
          enable = true
        }
        if (callback) {
          callback()
        }
      } else {
        $ebg.confirm({
          title: '',
          text: result['msg'],
          confirm: ''
        })
      }
    },
    error: function () {}
  })

  return enable
}
// GET
function appEncodeURL(s) {
  var reg = /^[\u0391-\uFFE5%]+$/
  var urlLength = s.length
  var newS = ''
  for (let i = 0; i < urlLength; i++) {
    if (reg.test(s.charAt(i))) {
      newS += escape(s.charAt(i))
    } else {
      newS += s.charAt(i)
    }
  }
  if (newS.indexOf('_app_encoding_tag_=') === -1) {
    if (newS.indexOf('?') >= 0) {
      newS = newS + '&_app_encoding_tag_=1'
    } else {
      newS = newS + '?_app_encoding_tag_=1'
    }
  }
  return encodeURI(newS)
}

const appClick = (data) => {
  if (!alertErrorIfStrEmpty(data.url, '')) return
  if (!alertErrorIfStrEmpty(data.name, '')) return
  if (!alertErrorIfStrEmpty(data.id, '')) return
  if (!alertErrorIfStrEmpty(data.target, '')) return
  ajax({
    url: data.hosturl + data.apiUrl.appStatus,
    type: 'post',
    // async: false, //,true
    data: { appId: data.id }, // json
    jsonp: 'jsonp', // jsonp
    success: function (res) {
      if (res['code']) {
        $ebg.confirm({
          title: '',
          text: res['code'] !== '-5101' ? res['msg'] : ',',
          confirm: ''
        })
        return
      }
      if (res['expired'] === 1) {
        // layer.alert(',');
        $ebg.confirm({
          title: '',
          text: ',',
          confirm: ''
        })
        return
      }
      // 3) URL/http:
      let isThirdApp = data.url.indexOf('/') !== 0
      if (isThirdApp) {
        if (!/^http(s)?/gi.test(data.url)) {
          data.url = 'http://' + data.url
        }

        // 0 
        appEnterable(data.id, data.hosturl, data.apiUrl, function () {
          // 1) urlurl
          if (!data.url) {
            // layer.alert('');
            $ebg.confirm({
              title: '',
              text: '',
              confirm: ''
            })
            return
          }

          // appClickCollectorLog($this);

          let windowOpen = window.open(data.url, data.target)
          if (windowOpen === null || typeof windowOpen === 'undefined') {
            $ebg.confirm({
              title: '',
              text: '',
              eventType: 'hideTips',
              btnMsg: ''
            })
          }
        })
        return
      }

      // append-param
      if (data.appendParam) {
        data.url = '/app/getInto.do?url=' + encodeURIComponent(data.url)
        data.url += '&appendParams=' + data.appendParam
        window.open(data.url, data.target)
        return
      }

      // 4appendText2Param=false
      //   '&_app_encoding_tag_=1&text=' + text + '
      if (data.url.indexOf('&appendText2Param=false') > 0) {
        data.url = data.url.replace('&appendText2Param=false', '')
      } else {
        if (data.url.indexOf('?') === -1) {
          data.url =
            data.url +
            appEncodeURL(
              '?_app_encoding_tag_=1&text=' +
                data.name +
                '&__time__=' +
                new Date().getTime()
            )
        } else {
          data.url =
            data.url +
            appEncodeURL(
              '&_app_encoding_tag_=1&text=' +
                data.name +
                '&__time__=' +
                new Date().getTime()
            )
        }
      }

      window.open(data.url, data.target)
    }
  })
}

export { appClick }
