const LogoutBtnEvent = (settings = []) => {
  const loginBtn = document.querySelector('.logout-btn')
  loginBtn &&
    loginBtn.addEventListener('click', () => {
      let logoutUrl = {}
      settings.some((item) => {
        logoutUrl = item
        return item.key === 'user-logout'
      })
      if (logoutUrl.key) {
        bindLogout(logoutUrl.url)
      } else {
        console.error('', logoutUrl)
      }
    })
}
