/**
 * 
 * @param {*} operate 
 * @param {*} delay 
 */
export const debounce = (fn, delay = 1000) => {
  let timer

  //  delay  func 
  return function () {
    // func
    const context = this
    const args = arguments

    // 
    clearTimeout(timer)

    // 
    //  delay  func
    timer = setTimeout(function () {
      fn.apply(context, args)
    }, delay)
  }
}
